import AOS from 'aos'
import { useEffect } from 'react';
import MyNavbar from "./MyNavbar";
import pic1 from '../images/bannerDesk.webp'
import pic2 from '../images/bannerDesk (1).webp'
import Search from "./Search";

function Home(){
    useEffect(() => {
        AOS.init();
    },[])
    return(
        <div  className="home" id="home">
        <MyNavbar />
        <div className="search-bar">
            <Search />
        </div>
        <div className="home-disp">
            <img src={pic1} data-aos="fade-up"/>
            <button className="bt">Explore</button>
            <img src={pic2} data-aos="fade-up"/>
        </div>
        </div>
    )
}

export default Home;