import { faFontAwesome } from "@fortawesome/free-brands-svg-icons";
function Contact(){
    return(
        <div className="contact" id="contact">
            <div className="form">
                <div className="contact-info">
                    <h1>Get in Touch!</h1>
                    <h2 className="con-h2">
We’re here to help! Whether you have questions about our T-shirts, need assistance with your order, or just want to share your thoughts, we’d love to hear from you.

Feel free to reach out using the form below. Our team will get back to you as soon as possible!

Thank you for visiting our store!</h2>

<div className="social">
<span className="in"></span>
</div>

                </div>
                <div className="contact-form">
                    <span className="circle one"></span>
                    <span className="circle two"></span>

                    <form>
                        <h3 className="title">Contact Us</h3>
                        <div className="input-container">
                            <input placeholder="User Name" type="text" name="name" className="input" />
                            <label for="">User Name</label>
                        </div>
                        <div className="input-container">
                            <input placeholder="email" type="email" name="email" className="input" />
                            <label for="">Email</label>
                        </div>
                        <div className="input-container">
                            <input placeholder="Mobile No" type="tel" name="phone" className="input" />
                            <label for="">Phone</label>
                        </div>
                        <div className="input-container">
                            <textarea placeholder="Message" name="Message" className="input" />
                            <label for="">Message</label>
                            <input type="submit" className="send" value='send' />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact;