import fb from '../images/facebook.png'
import insta from '../images/instagram.png'
import twit from '../images/twitter.png'
import yt from '../images/youtube.png'
function Footer(){
    return(
        <div className="footer">
           <div className="atracto can">
            <h2>Atracto</h2>
            <p>Welcome to Atracto, where style meets self-expression! Discover a unique collection of high-quality t-shirts that showcase your personality, passions, and creativity. From bold graphics to subtle designs, we’ve got something for everyone. Shop now and wear your story!</p>
            </div> 
            <div className="serve can">
                <h2>Our Services</h2>
                <ul>
                    <li>Men's T-shirt</li>
                    <li>Women's T-shirt</li>
                    <li>Kids T-shirt</li>
                </ul>
            </div>
            <div className="cont can">
                <h2>Contact us</h2>
                <ul>
                    <li>Address: Beside Barshal Water Tank, Manpur, Barhanti, West Bengal 723156</li>
                    <li>kkghosh0099@gmail.com</li>
                    <li>+91 9007062180</li>
                    
                </ul>
                <div className="sm">
                <img src={fb} alt="Face book" />
                <img src={insta} alt="Instagram" />
                <img src={yt} alt="yt" />
                <img src={twit} alt="Twitter"/>
            </div>
            </div>
        </div>
    )
}

export default Footer;