import './App.css';
import { motion, useScroll } from "framer-motion";
import Home from './components/Header'
import Explore from './components/Explore';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';
import Mens from './components/Mens';
import data from './data';
import Women from './components/Women';
import Kids from './components/Kids';
import Footer from './components/Footer';
import Contact from './components/Contact';
import women from './women'
import kids from './kids'



function App() {
  const { scrollYProgress } = useScroll();

  useEffect(() => {
    AOS.init();
  },[])

  const cards= data.map(item => {
    return(
      <Mens
      src={item.src}
      alt={item.alt}
      price={item.price}
      ogPrice={item.ogPrice}
      rating={item.rating}
      title={item.text}

       /**item = {item} */
     />

    )
  })

  const wcards= women.map(item => {
    return(
      <Women
      src={item.src}
      title={item.text}
      alt={item.alt}
      price={item.price}
      ogPrice={item.ogPrice}
      rating={item.rating}

       /**item = {item} */
     />

    )
  })

  const kcards= kids.map(item => {
    return(
      <Kids
      src={item.src}
      alt={item.alt}
      title={item.text}
      price={item.price}
      ogPrice={item.ogPrice}
      rating={item.rating}

       /**item = {item} */
     />

    )
  })


  return (
    <div className="App">
      <motion.div className="progress-bar" style={{ scaleX: scrollYProgress }} />
      <Home />
      <Explore data-aos="fade-left" />
      <h1 style={{textAlign:'center', paddingTop:'40px'}} id='men' data-aos='fade-up'>Men's Section</h1>
      <div className='me'>
      {cards}
      </div>
      <h1 style={{textAlign:'center', paddingTop:'40px'}} id='women' data-aos='fade-up'>Women's Section</h1>
      <div className='me'>
        {wcards}
      </div>
      <h1 style={{textAlign:'center', paddingTop:'40px'}} id='kid' data-aos='fade-up'>Kid's Section</h1>
      <div className='me'>
        {kcards}
      </div> 
      <Contact />
      <Footer /> 
    </div>
  );
}

export default App;
