import AOS from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';

import tsa from '../images/T-SHIRT-1.jpg'
import ts2 from '../images/images.jpeg'
import ts3 from '../images/images (1).jpeg'

import ts4 from '../images/-473Wx593H-464184683-white-MODEL.avif'
import ts5 from '../images/WWTS001174_1.jpg'
import ts6 from '../images/images (2).jpeg'

import ts7 from '../images/CaptainAmericaThemeCustomNameT-ShirtforKids.webp'
import ts8 from '../images/images (3).jpeg'
import ts9 from '../images/images (4).jpeg'
function Explore(){
    useEffect(() => {
        AOS.init();
    },[])
    return(
        <div className="explore" id='explore'>
            <h1>Explore</h1>
            <div className='adult'>
            <div data-aos="fade-right" className="boys-tshirt">
                <div>
                <img src={tsa} />
                </div>
                <div>
                    <img src={ts2} />
                </div>
                <div>
                    <img src={ts3} />
                </div>
                </div>
                <div data-aos="fade-right" className="boys-tshirt">
                <div>
                <img src={ts4} />
                </div>
                <div>
                    <img src={ts5} />
                </div>
                <div>
                    <img src={ts6} />
                </div>
                </div>
            </div>
            <div data-aos="fade-right" className='kids-sec'>
            <div className='boys-tshirt kids'>
            <div>
                <img src={ts7} />
                </div>
                <div>
                    <img src={ts8} />
                </div>
                <div>
                    <img src={ts9} />
                </div>
            </div>
            </div>
            <button className='bt ' >Explore More</button>
        </div>
    )
}

export default Explore;