import star from '../images/Red_star.svg.png'
import AOS from 'aos'
import { useEffect } from 'react';

function Kids(props){

    useEffect(()=>{
        AOS.init()
    },[])
    const srx = require(`../images/${props.src}`)
    return(
        <div className="mens-section" id='kids' data-aos="fade-right">
            <div className='cont'>
            </div>
        <div className='con'>
            <div className="container">
                <img src={srx} alt={props.alt.alt}/>
                <h1 className='product'>{props.title}</h1>
                <div className='rating'>
                    <img src={star} className='star'/>
                    <p className='rate'>{props.rating}</p>
                </div>
                <h2 className='price'>${props.price}<sub className='og-price'><s>{props.ogPrice}$</s></sub></h2>
                <button className='bt'>Order Now</button>
                </div>
            </div>
        </div>
    )
}

export default Kids;