import { Link } from "react-scroll" 
import cart from '../images/download.png'
import hamburger from '../images/hamburger.png'
function MyNavbar(){
    return(
    <div className="mob-naviner">
        <div className="nav-bar">
            <h1 className="logo-txt">Attract<span className="o">O</span></h1>
            <ul className="nav-elements">
                <li className="nav-links"><Link to="home" spy={true} smooth={true} offset={50} duration={500}>Home</Link></li>
                <li className="nav-links"><Link to="explore" spy={true} smooth={true} offset={50} duration={500}>Explore</Link></li>
                <li className="nav-links"><Link to="men" spy={true} smooth={true} offset={50} duration={500}>Men</Link></li>
                <li className="nav-links"><Link to="women" spy={true} smooth={true} offset={50} duration={500}>Women</Link></li>
                <li className="nav-links"><Link to="kid" spy={true} smooth={true} offset={50} duration={500}>Kids</Link></li>
                <li className="nav-links"><Link to="contact" spy={true} smooth={true} offset={50} duration={500}>Contact us</Link></li>
            </ul>
            <div className="profile">
                <img src="" />
                <img src={cart} className="cart" />
            </div>
        </div>
        <div className="mob-nav">
                <ul className="nav-elements">
                <li className="mob-nav"><Link to="home" spy={true} smooth={true} offset={50} duration={500}>Home</Link></li>
                <li className="mob-nav"><Link to="home" spy={true} smooth={true} offset={50} duration={500}>Explore</Link></li>
                <li className="mob-nav"><Link to="mens" spy={true} smooth={true} offset={50} duration={500}>Mens</Link></li>
                <li className="mob-nav"><Link to="womens" spy={true} smooth={true} offset={50} duration={500}>Women</Link></li>
                <li className="mob-nav"><Link to="kids" spy={true} smooth={true} offset={50} duration={500}>Kids</Link></li>
                <li className="mob-nav"><Link to="contact" spy={true} smooth={true} offset={50} duration={500}>Contact us</Link></li>
            </ul>
            </div>
    </div>
    );
}

export default MyNavbar;